<template>
    <div class="login-container">
        <div class="login_cont">
            <h3 class="login_title">auth-gateway 权限管理平台</h3>
            <el-card class="box-card">
                <div class="box-top">登 录</div>
                <el-form :model="loginForm" :rules="rules" ref="ruleForm" class="demo-ruleForm" @keyup.enter.native="submitForm('ruleForm')">
                    <el-form-item label="" prop="Mobile">
                        <el-input placeholder="用户名" prefix-icon="el-icon-user" type="text" v-model="loginForm.Mobile" autocomplete="off" class="loin_item"></el-input>
                    </el-form-item>
                    <el-form-item label="" prop="Password">
                        <el-input placeholder="密码" prefix-icon="el-icon-lock" show-password v-model="loginForm.Password" autocomplete="off" class="loin_item"></el-input>
                    </el-form-item>
                    <el-form-item label="" prop="Digits">
                        <el-input placeholder="验证码" prefix-icon="el-icon-picture-outline" v-model="loginForm.Digits" autocomplete="off" class="loin_item" ref="Digits">
                            <template slot="append">
                                <div class="img_cont" @click="getcook">
                                    <img :src="imgcode_url" alt="点击刷新" title="点击刷新" @load="imgload" @error="imgerror" v-show="isshowimg" />
                                    <div style="line-height:33px" v-if="!isshowimg">点击刷新</div>
                                </div>
                            </template>
                        </el-input>
                    </el-form-item>
                    <el-form-item>
                        <el-button type="primary" @click="submitForm('ruleForm')" class="loin_itembtn" :loading="loading">立即登录</el-button>
                        <!-- <el-button type="primary" @click="getUserinfo">个人信息</el-button> -->
                    </el-form-item>
                </el-form>
            </el-card>
        </div>
    </div>
</template>

<script>
export default {
    name: "Login",
    data() {
        return {
            loginForm: {
                Mobile: "",
                Password: "",
                Digits: "",
                CaptchaId:'',
            },
            imgcode_url: "",
            isshowimg: false,
            loading: false,
            rules: {
                Mobile: [
                    { required: true, trigger: "blur", message: "请输入用户名" }
                ],
                Password: [
                    { required: true, trigger: "blur", message: "请输入密码" }
                ],
                Digits: [
                    { required: true, trigger: "blur", message: "请输入验证码" }
                ]
            }
        };
    },
    created() {
        this.getcook();
    },
    mounted() {
        
    },
    methods: {
        imgload() {
            this.isshowimg = true;
        },
        imgerror() {
            this.isshowimg = false;
        },
        // 验证码
        changeImgCode() {
            this.$set(this.loginForm, "Digits", "");
            this.imgcode_url =
                "/admin/Captcha?captchaId="+this.CaptchaId+'&t='+String(new Date().getTime());
        },
        // 登录
        submitForm(formName) {
            this.$refs[formName].validate(valid => {
                if (valid) {
                    this.loading = true;
                    this.apiSever.post(this.URl.LOGIN, this.loginForm,res=> {
                        if(res.status==200){
                            this.$message.success("登录成功！");
                            this.$cookies.set("mytoken",    res.data.Token,"1d");
                            this.$router.push({ path: "/" });
                            localStorage.setItem("user_name", res.data.Name);
                            localStorage.setItem("user_mobile", res.data.Mobile);
                            localStorage.setItem(
                                "role_ids",
                                JSON.stringify(res.data.ID)
                            );
                            this.$store.commit("setuser", {
                                name: res.data.Name,
                                mobile: res.data.Mobile,
                                role_ids: res.data.ID
                            });
                        }else{
                            this.loading = false;
                        }
                        },error=>{
                            this.loading = false;
                        })
                } else {
                    console.log("error submit!!");
                    return false;
                }
            });
        },
        //设置验证码cook文件
        getcook() {
            this.apiSever.get(`${this.URl.ADMIN_CHECK_CODE}`, res => {
                this.CaptchaId = res.data
                this.loginForm.CaptchaId = res.data
                this.changeImgCode()
            })
        },
      
    }
};
</script>

<style lang="less" scoped>
.login-container {
    min-height: 100%;
    width: 100%;
    background: url("../../assets/images/login_bg.jpg") no-repeat center;
    background-size:cover;
    overflow: hidden;
    .login_cont {
        width: 1200px;
        height: 100vh;
        margin: auto;
        display: flex;
        align-items: center;
        justify-content: space-between;
        .login_title {
            text-align: center;
            font-size: 33.33px;
            letter-spacing: 4px;
            font-weight: bold;
            color: #ffffff;
        }
        .box-card {
            // background: transparent;
            border-radius: 12px;
            background-color: #fff;
            border: none;
            .box-top{
                margin-top: 68px;
                margin-bottom: 20px;
                margin-left: 20px;
                font-size: 25px;
                letter-spacing: 5px;
                color: #060606;
                font-weight: 600;
            }
            .demo-ruleForm {
                padding: 0 20px;
                &/deep/ .el-form-item{
                    margin-bottom: 30px;
                }
                .loin_item {
                    font-size: 18px;
                    /deep/ .el-input__inner {
                        height: 42px;
                        line-height: 42px;
                        font-size: 14px;
                    }
                    /deep/ .el-input__icon {
                        line-height: 42px;
                    }
                    /deep/ .el-input-group__append {
                        width: 100px;
                        padding: 0;
                        height: 42px;
                        box-sizing: border-box;
                        border: none;
                    }

                    .img_cont {
                        width: 100px;
                        height: 100%;
                        height: 42px;
                        // line-height: 25px;
                        font-size: 16px;
                        text-align: center;
                        box-sizing: border-box;
                        img {
                            width: 100%;
                            height: 100%;
                            cursor: pointer;
                            // object-fit: contain;
                        }
                    }
                }
                .loin_itembtn {
                    margin-top: 50px;
                    width: 100%;
                    font-size: 18px;
                }
            }
        }
    }
}
</style>
